@import "@styles/variables.scss";

.hide {
  display: none;
}

.show {
  display: block;
}

.header {
  width: 100%;
  height: 88px;
  color: $color-greyscale-white;
  position: relative;
  .menuControl {
    position: absolute;
    z-index: -2;
    opacity: 0;
  }
  .menuBtn {
    width: 50px;
    height: 43px;
    background-color: transparent;
    display: none;
    cursor: pointer;
    span {
      opacity: 0;
    }
    &::before {
      content: "";
      position: absolute;
      height: 2.5px;
      width: 26px;
      left: 12px;
      background-color: $color-greyscale-400;
      // 置中
      top: 0;
      bottom: 0;
      margin: auto;

      box-shadow: 0px 8px 0px $color-greyscale-400,
        0px -8px 0px $color-greyscale-400;
    }
  }
  .container {
    width: 1200px;
    height: 100%;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    align-items: center;
    position: relative;
    .headerLogo {
      width: 231px;
      height: 32px;
    }
    // .navMenu{

    // }
  }
}

.menuLandscape {
  margin-right: 40px;
  text-decoration: none;
  position: relative;
  &:hover {
    color: $color-primary-default;
    &::after {
      content: url(../../asset/icon/wave_blue.png);
      position: absolute;
      top: 20px;
      right: 0;
    }
  }
  &:after {
    color: $color-primary-default;
  }
}

.menuFair {
  position: relative;
  .menuFairLink {
    text-decoration: none;
    margin-right: 40px;
    &:hover {
      color: $color-secondary-yellow-default;
    }
  }
  &:hover {
    &::after {
      content: url(../../asset/icon/wave_yellow.png);
      position: absolute;
      top: 20px;
      right: 40px;
    }
    .menuDropdown1 {
      display: block;
    }
  }
}

.menuFood {
  margin-right: 40px;
  text-decoration: none;
  position: relative;
  &:hover {
    color: $color-secondary-pink-default;
    &::after {
      content: url(../../asset/icon/wave_pink.png);
      position: absolute;
      top: 20px;
      right: 0;
    }
  }
}

.menuTraffic {
  position: relative;
  .menuTrafficLink {
    margin-right: 40px;
    text-decoration: none;
    &:hover {
      color: $color-primary-default;
    }
  }
  &:hover {
    &::after {
      content: url(../../asset/icon/wave_blue.png);
      position: absolute;
      top: 20px;
      right: 40px;
    }
    .menuDropdown2 {
      display: block;
    }
  }
}
.menuDropdown1 {
  width: 175px;
  background: #ffffff;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  position: absolute;
  left: -50px;
  top: 40px;
  padding: 15px 30px 15px 30px;
  display: none;
  li {
    padding: 9px 0 9px 0;
    display: flex;
    align-items: center;
    span {
      padding-right: 12px;
    }
    a {
      text-align: none;
      text-decoration: none;
      &:hover {
        color: $color-secondary-yellow-default;
        font-weight: $font-weight-bold;
      }
    }
  }
}

.menuDropdown2 {
  width: 175px;
  background: $color-greyscale-white;
  box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  position: absolute;
  left: -50px;
  top: 40px;
  padding: 15px 30px 15px 30px;
  display: none;
  li {
    padding: 9px 0 9px 0;
    display: flex;
    align-items: center;
    span {
      padding-right: 12px;
    }
    a {
      padding: 9px 0 9px 0;
      display: flex;
      align-items: center;
      text-decoration: none;
      &:hover {
        color: $color-primary-default;
        font-weight: $font-weight-bold;
      }
    }
  }
}

.btnCallForAction {
  background-color: $color-primary-default;
  width: 134px;
  height: 42px;
  font-weight: $font-weight-medium;
  color: $color-text-white;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  &:hover {
    background-color: $color-primary-dark;
  }
  &:active {
    background-color: $color-primary-dark;
  }
}

@media (max-width: 1439px) and (min-width: 1200px) {
  .header {
    .container {
      width: 100%;
    }
  }
}

@media (max-width: 1199px) and (min-width: 768px) {
  .header {
    .menuControl {
      &:checked ~ .container .navMenu {
        left: -32px;
      }
    }
    .container {
      width: 100%;
      justify-content: start;
      a {
        margin-left: calc((100vw - 345px) / 2);
      }
      .menuBtn {
        display: block;
      }
      .navMenu {
        width: 40vw;
        height: calc(100vh - 50px);
        background-color: $color-primary-tint1;
        position: absolute;
        top: 88px;
        left: -500px;
        z-index: 2;
        transition: 0.5s;
        .menuLandscape {
          display: block;
          border-bottom: 1px solid $color-greyscale-white;
          margin-left: 24px;
          padding-top: 24px;
          padding-bottom: 24px;
          &:hover {
            &::after {
              content: "";
            }
          }
        }
        .menuFair {
          .menuFairLink{
            display: block;
            border-bottom: 1px solid $color-greyscale-white;
            margin-left: 24px;
            padding-top: 24px;
            padding-bottom: 24px;
            &:hover {
              &::after {
                content: "";
              }
            }
          }
          .menuDropdown1 {
            display: none;
          }
        }
        .menuFood {
          display: block;
          border-bottom: 1px solid $color-greyscale-white;
          margin-left: 24px;
          padding-top: 24px;
          padding-bottom: 24px;
          &:hover {
            &::after {
              content: "";
            }
          }
        }
        .menuTraffic {
          .menuTrafficLink{
            display: block;
            border-bottom: 1px solid $color-greyscale-white;
            margin-left: 24px;
            padding-top: 24px;
            padding-bottom: 24px;
            &:hover {
              &::after {
                content: "";
              }
            }
          }
          .menuDropdown2 {
            display: none;
          }
        }
        .btnCallForAction {
          margin-left: 24px;
          margin-top: 24px;
        }
      }
    }
  }
}
